<template>
  <div>
    <el-card>
      <div slot="header">基本信息
        <el-button style="float: right" type="info" size="small" @click="$router.go(-1)">返回</el-button>
      </div>
      <div class="tablediv">

          <table border="1">
            <tbody>

                <tr>
                    <td width="105" class="key">负责人</td>
                    <td width="280" title="张伟峰">
                        {{$route.query.name}}
                    </td>
                  
                
                    <td width="105" class="key">学科门类</td>
                    <td width="280" :title="this.Form.subjectCategory">
                        {{Form.subjectCategory}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">项目名称</td>
                    <td width="280" :title="this.Form.tpName">
                        {{Form.tpName}}
                    </td>
                  
                
                    <td width="105" class="key">项目编号</td>
                    <td width="280" title="1991-09-07">
                        {{Form.tpNumber}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">负责人类型</td>
                    <td width="280" title="2010-10-01">
                        {{Form.principalType}}
                    </td>
                  
                
                    <td v-if="this.istea == 1" width="105" class="key">所属单位</td>
                    <td v-else width="105" class="key">所属学院</td>
                    <td width="280" title="2020-04-29">
                        {{Form.belongDept}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">项目级别</td>
                    <td width="280" title="2019-12-01">
                        {{Form.projectLevel}}
                    </td>
                  
                
                    <td width="105" class="key">项目分类</td>
                    <td width="280" title="市场">
                        {{Form.peojectType}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">结项编号</td>
                    <td width="280" title="总裁办">
                        {{Form.closingNumber}}
                    </td>
                  
                
                    <td width="105" class="key">批准号</td>
                    <td width="280" title="人事">
                        {{Form.approvalNumber}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">项目来源单位</td>
                    <td width="280" title="A1">
                        {{Form.projectSourceUnit}}
                    </td>
                  
                
                    <td width="105" class="key">承担单位排名</td>
                    <td width="280" title="">
                        {{Form.undertakingRank}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">项目状态</td>
                    <td width="280" title="公司薪酬规则">
                        {{Form.projectStatus}}
                    </td>
                  
                
                    <td width="105" class="key">立项日期</td>
                    <td width="280" title="4445778900766">
                        {{Form.projectDate}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">项目开始时间</td>
                    <td width="280" title="66789965433">
                        {{Form.projectStartTime}}
                    </td>
                  
                
                    <td width="105" class="key">计划完成日期</td>
                    <td width="280" title="344455566778888">
                        {{Form.planFinishDate}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">成果形式</td>
                    <td width="280" title="">
                        {{Form.resultForm}}
                    </td>
                  
                
                    <td width="105" class="key">合同类型</td>
                    <td width="280" title="512156199109073227">
                        {{Form.contractType}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">合同经费</td>
                    <td width="280" title="3220445666789900">
                        {{Form.contractFunding}}
                    </td>
                  
                
                    <td width="105" class="key">财务账号</td>
                    <td width="280" title="本科">
                        {{Form.financialAccount}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">配套经费（单位:万元）</td>
                    <td width="280" title="未婚">
                        {{Form.supportingExpenses}}
                    </td>
                  
                
                    <td width="105" class="key">自筹经费（单位:万元）</td>
                    <td width="280" title="党员">
                        {{Form.seltFinancing}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">专项经费（单位:万元）</td>
                    <td width="280" title="天津市">
                        {{Form.specialFunds}}
                    </td>
                  
                <td width="105" class="key">备注</td>
                <td width="280" title="">
                    {{Form.remarksInfo}}
                </td>
            </tr>

            </tbody>
        </table>
      </div>
    </el-card>

    <el-card>
      <div slot="header">作者信息</div>
      <div>
        <el-table
          :data="authForm"
          border
          stripe
        >
          <el-table-column
          prop="authorType"
          label="作者类型"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="authorName"
          label="作者姓名"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="authorId"
          label="作者工号"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="firstLevelDisc"
          label="一级学科"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="authorSex"
          label="性别"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="signingOrder"
          label="署名顺序"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="authorEducation"
          label="学历"
          align="center"
        ></el-table-column>

        <el-table-column
        v-if="this.istea == 1"
          prop="authorTitle"
          label="职称"
          align="center"
        ></el-table-column>

        <el-table-column
        v-if="this.istea == 1"
          prop="authorWorkUnit  "
          label="工作单位"
          align="center"
        ></el-table-column>
        

        <el-table-column
          prop="contributionRate"
          label="贡献率：（ ）单位：%"
          align="center"
        ></el-table-column>

        </el-table>
      </div>
    </el-card>

    

  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      authForm:{},
      accessory:[],
      Options:[],
      Form: {},
      authForm:[],
      List: [],
      istea : undefined,
      look : undefined
    }
  },
  created() {
    this.isAuthority()
      this.getDetail()
      this.getTreeselect()
      this.getAccessory()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    delAccessory(row) {
      if(row.id){var a =row.id.split(',')}
      const userid = a || this.ids;
      console.log(userid);
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(
            '/accessoriesList/removeByIdAccessoriesList/' + userid,
          )
          // console.log(res);
          this.getDetail()
          this.Form = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },

    delAuth(row) {
      if(row.id){var a =row.id.split(',')}
      const userid = a || this.ids;
      console.log(userid);
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(
            '/projectAuthorInfo/removeByIdProjectAuthorInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.getDetail()
          this.Form = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    async getAccessory(){
      const { data: res } = await this.$http.get(
        '/accessoriesList/getByIdAccessoriesList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.accessory = res.data.entity
      console.log(this.accessory);
    },

    async getTreeselect() {
      this.istea = this.$route.query.istea
      const id = this.$route.query.id
      // console.log(istea);
      if (this.istea == 1) {
        const { data: res } = await this.$http.post(
          '/teaBaseInfo/queryTeaBaseInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        this.Options = res.data.dataList.records
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }else if(this.istea == 0){
        const { data: res } = await this.$http.post(
          '/studentInfo/queryStudentInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        let a = res.data.dataList.records
        a = JSON.parse(JSON.stringify(a).replace(/stuId/g,'teaId'))
        this.Options = JSON.parse(JSON.stringify(a).replace(/stuName/g,'teaName'))
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }

    },

    async getDetail() {
      if (!this.$route.query.id.length) {
        return
      }
      const id = this.$route.query.id
      // console.log(id)
      const { data: res } = await this.$http.get(
        '/projectInfo/getByIdProjectInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.Form = res.data.entity
      console.log(this.Form)
      this.getAutherinfo()
      this.getAccessory()
    },
    async getAutherinfo(){
      const { data: res } = await this.$http.get(
        '/projectAuthorInfo/queryProjectAuthorInfoList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.authForm = res.data.dataList
      console.log(this.authForm);
    },
    submitForm() {
      if (this.istea == 1)this.Form.isStu = 1
      else if(this.istea == 0)this.Form.isStu = 0
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/projectInfo/saveOrUpdateProjectInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.getDetail()
          return this.$message.success(res.message)
        }
      })
    },
    
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-bottom: 20px;
}
.save{
  float: right;
  margin-right: 10px;
}
table{
    border: 1px solid #d3d3d3;
    border-collapse: collapse;
    width: 100%;
    color: #606266;
    line-height: 45px;
    td{
        padding-left: 22px;
    }
}
.key{
    color: #909399;
    background-color: #f9f9f9;
}
</style>
